import { Joi, celebrate } from 'celebrate';
import express from 'express';
import forgotPassword from '../controllers/forgotpassword_post';
import { baseUrl } from '../baseurl';

const router = express.Router();
const requestSchema = {
  body: {
    email: Joi.string().email().required(),
  },
};
/**
 * @swagger
 * /auth/forgotPassword:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns status 200 if email is sent successfully
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "email"
 *         description: "Email address of a user."
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              email :
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           properties:
 *              message:
 *                  type: string
 *       404:
 *         description: Returns 404 if the email does not belong to any user
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 404
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(
  `${baseUrl}/forgotPassword`,
  celebrate(requestSchema),
  forgotPassword
);

export default router;
